













































export default {
	data() {
		return {}
	},
	components: {},
	methods: {
		weixin() {
			uni.login({
				provider: 'weixin',
				success: function(loginRes) {
					console.log(JSON.stringify(loginRes));
					uni.getUserInfo({
						provider: 'weixin',
						success: function(infoRes) {
							uni.setStorageSync('user_info', infoRes);
							uni.navigateTo({
								url: '/pages/index/index'
							})
							
						}
					});
				}
			});
		}
	},
	mounted() {},
}



